@import url('https://fonts.googleapis.com/css2?family=Abril+Fatface&family=Montserrat:wght@200;300&family=Nunito:wght@200;300;900&family=Outfit:wght@300;500;900&display=swap');

body {
  background-image: linear-gradient(120deg, #fdfbfb 0%, #ebedee 100%);
  font-family: 'Abril Fatface', cursive;
  font-family: 'Montserrat', sans-serif;
  font-family: 'Nunito', sans-serif;
  font-family: 'Outfit', sans-serif;
}

.custom {
  padding: 0% !important;
}

.heading {
  border: 1px solid black;
  box-shadow: 10px -10px 0 -4px #488eff, 10px -10px 0 0 rgb(41, 41, 41);
  background-color: white;
}